.l-perfil__image {
  .move-1 {
    max-width: 100%;
    animation: move-1 5s infinite;
    margin: 0;
    padding: 0;
    line-height: 0;
  }

  .move-2 {
    max-width: 100%;
    animation: move-2 5s infinite;
    top: 10px;
    margin: -1.1px;
    padding: -1.1px;
    line-height: 0;
  }
  .move-3 {
    max-width: 100%;
    animation: move-3 5s infinite;
    margin: 0;
    padding: 0;
    line-height: 0;
  }
  .move-4 {
    max-width: 100%;
    animation: move-4 5s infinite;
    margin: -1px;
    padding: -1px;
    line-height: 100px;
  }
  .move-5 {
    max-width: 100%;
    animation: move-5 5s infinite;
    margin: -1px;
    padding: -1px;
    line-height: 0;
  }
}

@keyframes move-1 {
  0% {
    transform: translateX(0px);
  }
  1% {
    transform: translateX(15px);
  }
  2% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(0px);
  }
}

@keyframes move-2 {
  0% {
    transform: translateX(1px);
  }
  1% {
    transform: translateX(-12px);
  }
  2% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(0px);
  }
}

@keyframes move-3 {
  1% {
    transform: translateX(2px);
  }
  2% {
    transform: translateX(7px);
  }
  3% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(0px);
  }
}

@keyframes move-4 {
  0% {
    transform: translateX(0px);
  }
  2% {
    transform: translateX(-3px);
  }
  3% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(0px);
  }
}

@keyframes move-5 {
  0% {
    transform: translateX(1px);
  }
  1% {
    transform: translateX(2px);
  }
  2% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(0px);
  }
}
